// /frontend/src/features/simulation/SimulationStatsPage.jsx
import React, { useEffect, useState } from 'react';
import Papa from 'papaparse';
import { Tooltip, TooltipProvider } from 'react-tooltip';
import legendMap from '../data/legend_map.json'; // Adjust the path if necessary

// Define the stat keys (with the "r" prefix)
const statKeys = [
  "rDD", "rDS", "rOTD", "rAST", "rULT", "rLVS", "rLLS", "rDFS",
  "rKNB", "rCTT", "rEVS", "rFFD", "rFFI", "rSNP", "rCQT", "rBRX",
  "rHWI", "rMOT", "rAMB", "rMMB", "rILS", "rFE", "rDSR", "rSLP", "rRSP"
];

export default function SimulationStatsPage() {
  const [data, setData] = useState([]);
  const [teamMapping, setTeamMapping] = useState({});
  const [filterText, setFilterText] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [sortColumn, setSortColumn] = useState('');
  const [sortOrder, setSortOrder] = useState('asc');
  const [rowsPerPage, setRowsPerPage] = useState(20);

  // Load simulation results from the JSON file
  useEffect(() => {
    fetch('/data/simulation_results.json')
      .then(res => res.json())
      .then(json => {
        console.log("Simulation results loaded:", json);
        setData(json.filter(row => row.match_id && row.character && row.stats));
      })
      .catch(err => console.error("Error loading simulation results JSON:", err));
  }, []);

  // Load team mapping from teamIDs.csv
  useEffect(() => {
    Papa.parse('/data/teamIDs.csv', {
      header: true,
      download: true,
      complete: (results) => {
        const map = {};
        results.data.forEach(row => {
          // Expect CSV headers: team_ids, teamName
          if (row.team_ids && row.teamName) {
            map[row.team_ids.toUpperCase()] = row.teamName;
          }
        });
        console.log("Team mapping loaded:", map);
        setTeamMapping(map);
      },
      error: (err) => console.error("Error loading teamIDs.csv:", err)
    });
  }, []);

  // Filter data based on search text (searching across team, Nexus Being, and stat values)
  const filteredData = data.filter(row => {
    const teamCode = row.match_id.split('_')[0].toUpperCase();
    const teamName = teamMapping[teamCode] || teamCode;
    const statsStr = row.stats ? Object.values(row.stats).join(" ") : "";
    const searchString = `${teamName} ${row.character} ${statsStr}`.toLowerCase();
    return searchString.includes(filterText.toLowerCase());
  });

  // Sorting logic: sort by Team, Nexus Being, or a stat key.
  const sortedData = [...filteredData].sort((a, b) => {
    if (!sortColumn) return 0;
    let aVal = '';
    let bVal = '';
    if (sortColumn === 'Team') {
      const aTeamCode = a.match_id.split('_')[0].toUpperCase();
      const bTeamCode = b.match_id.split('_')[0].toUpperCase();
      aVal = teamMapping[aTeamCode] || aTeamCode;
      bVal = teamMapping[bTeamCode] || bTeamCode;
    } else if (sortColumn === 'Nexus Being') {
      const stripTeamCode = (name) => name.replace(/^T\d+\s+/, '');
      aVal = stripTeamCode(a.character || '');
      bVal = stripTeamCode(b.character || '');
    } else {
      // For stat columns, assume sortColumn is the friendly stat name (without 'r')
      aVal = (a.stats && a.stats[`r${sortColumn}`]) || 0;
      bVal = (b.stats && b.stats[`r${sortColumn}`]) || 0;
    }
    return sortOrder === 'asc'
      ? aVal.toString().localeCompare(bVal.toString(), undefined, { numeric: true })
      : bVal.toString().localeCompare(aVal.toString(), undefined, { numeric: true });
  });

  // Calculate pagination details
  const totalPages = Math.ceil(sortedData.length / rowsPerPage);
  const paginatedData = sortedData.slice(
    (currentPage - 1) * rowsPerPage,
    currentPage * rowsPerPage
  );

  const handleSort = (column) => {
    if (sortColumn === column) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setSortColumn(column);
      setSortOrder('asc');
    }
  };

  // Build headers: fixed "Team" and "Nexus Being" plus stat keys (without the leading "r")
  const headers = ["Team", "Nexus Being", ...statKeys.map(key => key.substring(1))];

  // Utility: Render a cell's value based on the column.
  const renderCellValue = (column, row) => {
    if (column === "Team") {
      const teamCode = row.match_id.split('_')[0].toUpperCase();
      return teamMapping[teamCode] || teamCode;
    }
    if (column === "Nexus Being") {
      return row.character.replace(/^T\d+\s+/, '');
    }
    const originalKey = `r${column}`;
    return row.stats && row.stats[originalKey] !== undefined ? row.stats[originalKey] : 0;
  };

  // Create an array of page numbers for direct navigation.
  const pageNumbers = [];
  for (let i = 1; i <= totalPages; i++) {
    pageNumbers.push(i);
  }

  return (
    <TooltipProvider>
      <div className="p-4 bg-gradient-to-r from-gray-100 to-blue-50 min-h-screen">
        <h2 className="text-3xl font-marvel mb-6">Simulation Statistics</h2>

        <div className="flex flex-col md:flex-row md:justify-between items-center mb-4">
          <input
            type="text"
            placeholder="Search simulation results..."
            className="p-2 mb-4 md:mb-0 border rounded w-full md:w-1/2"
            value={filterText}
            onChange={(e) => {
              setFilterText(e.target.value);
              setCurrentPage(1);
            }}
          />
          <div className="flex items-center">
            <label htmlFor="rowsPerPage" className="mr-2">
              Rows per page:
            </label>
            <select
              id="rowsPerPage"
              className="border rounded p-1"
              value={rowsPerPage}
              onChange={(e) => {
                setRowsPerPage(Number(e.target.value));
                setCurrentPage(1);
              }}
            >
              <option value={10}>10</option>
              <option value={20}>20</option>
              <option value={50}>50</option>
              <option value={100}>100</option>
            </select>
          </div>
        </div>

        {filteredData.length === 0 ? (
          <p>Loading simulation results...</p>
        ) : (
          <>
            <table className="min-w-full text-sm border-collapse border border-gray-400">
              <thead>
                <tr className="bg-blue-200">
                  {headers.map((header, idx) => (
                    <th
                      key={idx}
                      className="px-2 py-1 border border-gray-300 cursor-pointer"
                      onClick={() => handleSort(header)}
                    >
                      {(header === "Team" || header === "Nexus Being")
                        ? header
                        : <span data-tooltip-id="legend-tooltip" data-tooltip-content={legendMap[header] || header}>
                            {header}
                          </span>
                      }
                      {sortColumn === header && (sortOrder === 'asc' ? ' ▲' : ' ▼')}
                    </th>
                  ))}
                </tr>
              </thead>
              <tbody>
                {paginatedData.map((row, rowIndex) => (
                  <tr key={rowIndex} className={rowIndex % 2 === 0 ? 'bg-white' : 'bg-gray-100'}>
                    {headers.map((header, idx2) => (
                      <td key={idx2} className="px-2 py-1 border border-gray-300">
                        {renderCellValue(header, row)}
                      </td>
                    ))}
                  </tr>
                ))}
              </tbody>
            </table>

            <div className="mt-4 flex flex-col md:flex-row justify-between items-center text-sm">
              <div className="flex items-center space-x-2 mb-2 md:mb-0">
                <button
                  onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                  disabled={currentPage === 1}
                  className="px-2 py-1 border rounded disabled:opacity-50"
                >
                  Previous
                </button>
                {pageNumbers.map(number => (
                  <button
                    key={number}
                    onClick={() => setCurrentPage(number)}
                    className={`px-2 py-1 border rounded ${currentPage === number ? 'bg-blue-500 text-white' : 'bg-white'}`}
                  >
                    {number}
                  </button>
                ))}
                <button
                  onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                  disabled={currentPage === totalPages}
                  className="px-2 py-1 border rounded disabled:opacity-50"
                >
                  Next
                </button>
              </div>
              <span>
                Page {currentPage} of {totalPages}
              </span>
            </div>
          </>
        )}
        <Tooltip id="legend-tooltip" place="top" type="dark" effect="solid" multiline />
      </div>
    </TooltipProvider>
  );
}
